.reg-button {
    font-family: 'RobotoFlex';
    font-weight: 700;
    font-size: 32px;
    color: #FFFFFF;
    padding: 27px 64px;
    background-color: #EA005A;
    border-radius: 4px;
    text-decoration: none;
    transition: all 0.2s ease;
    display: inline-block;

    &:hover{
        background-color: #FF2377;
    }
}

body{
    background-color: #000C32;
    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }
}

.container {
    width: 1440px;
    margin: 0 auto;
}

.nav {
    padding: 32px 0px;
    margin-bottom: 170px;

    &__title {
        position: relative;
        bottom: 0.75rem;
        float: left;
        font-family: 'DelaGothicOne';
        font-weight: 400;
        font-size: 40px;
        color: #FFFFFF;
    }

    &__menu {
        float: right;
        margin: 0;
        padding: 0;
        
        &__item {
            display: inline;
        }

        &__item:not(:last-child) {
            margin-right: 48px;
        }

        &__link {
            font-family: 'Inter';
            font-weight: 600;
            font-size: 24px;
            color: #FFFFFF;
            text-decoration: none;
        }
    }
    
}

.header {
    background-image: url('../img/header-background.png');
    //background-image: url('../img/header-background.webp');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    margin-bottom: 120px;
    padding-bottom: 200px;

    &__title {
        text-align: left;
        font-family: 'DelaGothicOne';
        font-weight: 400;
        font-size: 96px;
        color: #FFFFFF;
        margin-bottom: 16px;

        &__anticolor {
            color: #FFFF00;
        }
    }
    
    &__description {
        font-family: 'Inter';
        font-weight: 300;
        font-size: 32px;
        color: #FFFFFF;
        max-width: 776px;
        margin-bottom: 32px;
    }

    &__date {
        font-family: 'RobotoFlex';
        font-weight: 700;
        font-size: 32px;
        color: #FFFFFF;
        max-width: 320px;
        margin-bottom: 40px;
    }

    &__social {
        float: right;
        font-family: 'Inter';
        font-weight: 300;
        font-size: 32px;
        text-align: right;
        color: #FFFFFF;

        &__item:not(:last-child) {
            margin-bottom: 1rem;
        }
    }

}

.main {
    //padding: 0 64px;
    &__whatis {
        margin-bottom: 120px;
        &__title{
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            text-align: center;
            color: #FFFFFF;
            margin-bottom: 32px;
        }
        &__description {
            font-family: 'Inter';
            font-weight: 300;
            font-size: 24px;
            color: #FFFFFF;
            width: 60%;
            text-align: center;
            margin: 0 auto;
            margin-bottom: 64px;
        }
        &__photos {
            // display: grid;
            // grid-template-columns: repeat(3, 463px);
            // gap: 26px;
            // align-content: center;
            display: flex;
            justify-content: center;
            gap: 26px;
            &__item {
                display: inline;
            }
        }
    }

    &__whatwait {
        margin-bottom: 120px;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 64px;
        }
        &__list {

            display: flex;
            justify-content: space-around;
            gap: 26px;

            &__item {
                background-color: #EA005A;
                border-radius: 4px;
                padding: 64px 32px;
                text-align: center;
                //width: 100%;
                min-width: 425px;

                &__icon {
                    margin-bottom: 42px;
                }

                &__title {
                    font-family: 'RobotoFlex';
                    font-weight: 800;
                    font-size: 32px;
                    color: #FFFFFF;
                    margin-bottom: 32px;
                }

                &__description {
                    font-family: 'Inter';
                    font-weight: 300;
                    // font-weight: 500;
                    font-size: 24px;
                    color: #FFFFFF;
                    text-align: left;
                }
            }
        }
    }

    &__prize {
        margin-bottom: 120px;
        position: relative;
        &__bg {
            position: absolute;
            right: 25px;
            top: 50px;
            width: 50%;
            height: auto;
            z-index: -1;
        }
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 48px;
        }
        &__description {
            font-family: 'RobotoFlex';
            font-weight: 700;
            font-size: 48px;
            color: #FFFF00;
            text-align: center;
            margin-bottom: 80px;
        }
        &__top {
            margin-bottom: 250px;
            &__item {
                &__title {
                    font-family: 'RobotoFlex';
                    font-weight: 700;
                    font-size: 40px;
                    color: #FFFFFF;
                }
                &__description {
                    font-family: 'Inter';
                    font-weight: 300;
                    font-size: 24px;
                    color: #FFFFFF;
                }
            }
            &__item:not(:last-child) {
                margin-bottom: 32px;
            }
        }
    }

    &__forwhom {
        margin-bottom: 120px;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 32px;
        }
        &__description {
            font-family: 'Inter';
            font-weight: 300;
            font-size: 24px;
            text-align: center;
            color: #FFFFFF;
            margin-bottom: 64px;

            &__anticolor {
                color: #FFFF00;
            }
        }
        &__teams {
            &__item {
                &__info {
                    max-width: 50%;
                    &__title {
                        font-family: 'RobotoFlex';
                        font-weight: 700;
                        font-size: 40px;
                        color: #FFFFFF;
                        margin-bottom: 12px;
                    }
                    &__description {
                        font-family: 'Inter';
                        font-weight: 300;
                        font-size: 24px;
                        color: #FFFFFF;

                        &__link {
                            transition: all 0.2s ease;
                            color: #EA005A;
                            &:hover {
                                color: #D20755;
                            }
                        }
                    }
                }
                &__date {
                    font-family: 'RobotoFlex';
                    font-weight: 700;
                    font-size: 36px;
                    color: #FFFFFF;
                }
            }
            &__item:not(:last-child) {
                display: flex;
                justify-content: space-between;
                align-content: center;
                margin-bottom: 32px;
            }
        }
    }

    &__case {
        margin-bottom: 120px;
        position: relative;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 62px;
        }
        &__cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;

            &__card {
                position: relative;
                background-color: #EA005A;
                border-radius: 4px;
                padding: 20px;
                min-height: 424px;
                max-height: 424px;
                transition: all 0.3s ease;
                overflow: hidden;

                &__title {
                    font-family: 'RobotoFlex';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 40px;
                    color: #FFFFFF;
                    margin-bottom: 25px;
                }

                &__description {
                    display: none;
                    opacity: 0;
                    animation: fadein .4s;
                    font-family: 'Inter';
                    font-weight: 500;
                    font-size: 24px;
                    color: #FFFFFF;
                    margin-bottom: 120px;
                }

            
                .active__description {
                    opacity: 1;
                    display: block;
                }

                &__number {
                    position: absolute;
                    right: 20px;
                    bottom: 20px;
                    font-family: 'DelaGothicOne';
                    font-weight: 400;
                    font-size: 96px;
                    color: #FFFF00;
                }
            }
            
            .active__card {
                min-height: 750px;
            }

            @keyframes fadein {
                from {opacity: 0;}
                to {opacity: 1;}
            }
        }
    }

    &__whatdo {
        margin-bottom: 120px;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 64px;
        }
        &__todo {
            &__item {
                display: grid;
                grid-template-columns: 1fr 9fr;
                justify-items: left;
                &__icon {
                    grid-row: span 2;
                    margin-right: 32px;
                }
                &__title {
                    font-family: 'RobotoFlex';
                    font-weight: 700;
                    font-size: 40px;
                    color: #FFFFFF;
                    margin-bottom: 16px;
                }
                &__description {
                    font-family: 'Inter';
                    font-weight: 300;
                    font-size: 24px;
                    color: #FFFFFF;
                }
            }
            &__item:not(:last-child) {
                margin-bottom: 64px;
            }
        }
    }

    &__experts {
        margin-bottom: 120px;
        color: #FFFFFF;
        text-align: center;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            margin-bottom: 64px;
        }
        &__peoples {
            margin-bottom: 64px;
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 20px;
            &__item {
                &__photo {
                    border-radius: 100%;
                    margin-bottom: 16px;
                }
                &__title {
                    font-family: 'RobotoFlex';
                    font-weight: 700;
                    font-size: 32px;
                    margin-bottom: 20px;
                }
                &__description {
                    font-family: 'Inter';
                    font-weight: 300;
                    font-size: 24px;
                }
            }
        }
        &__companies {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 80px;
        }
    }
    &__timeline {
        margin-bottom: 120px;
        color: #FFFFFF;
        &__title {
            font-family: 'DelaGothicOne';
            font-weight: 400;
            font-size: 64px;
            margin-bottom: 64px;
            text-align: center;
        }
        &__list {
            &__item {
                display: grid;
                grid-template-columns: 1fr 6fr;
                gap: 56px;
                &__number {
                    font-family: 'DelaGothicOne';
                    font-weight: 400;
                    font-size: 96px;
                    color: #FFFF00;
                }
                &__info {
                    padding-left: 20px;
                    border-left: 3px solid #FFFFFF;
                    &__title {
                        font-family: 'RobotoFlex';
                        font-weight: 700;
                        line-height: 56px;
                        font-size: 40px;
                        margin-bottom: 16px;
                    }
                    &__description {
                        font-family: 'Inter';
                        font-weight: 300;
                        font-size: 24px;
                    }
                }
                &:not(:last-child) &__info {
                    padding-bottom: 95px;
                }
            }
        }
    }
}

.footer {
    text-align: center;
    &__button {
        margin-bottom: 90px;
    }

    &__emblem {
        margin-bottom: 64px;
    }

    &__title {
        font-family: 'DelaGothicOne';
        font-weight: 400;
        font-size: 64px;
        color: #FFFFFF;
        margin-bottom: 32px;
    }

    &__number {
        font-family: 'RobotoFlex';
        font-style: normal;
        font-weight: 700;
        font-size: 48px;
        color: #FFFF00;
        margin-bottom: 50px;
    }

    &__icon {
        margin-bottom: 80px;
    }
}